/* components/GameScreen/GameScreen.css */

.game-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between; /* Ensures that the content is spaced out */
    height: 100vh; /* Take up full viewport height */
    max-height: 852px;
    gap: 20px;
}

.question-content-container {
    width: 100%;

}

.movie-still-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 12px;
    flex: 1; /* This allows the container to take up all the available space */
    padding-bottom: 24px;
}

.movie-still {
    width: 100%;
    padding-top: 56.25%; /* 16:9 aspect ratio */
    position: relative;
    overflow: hidden;
}

.movie-still img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 4px;
}

.title-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 40px;
}

.circle-indicators {
    display: flex;
    gap: 8px;
    flex-shrink: 0;
}

.circle {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: #EBEBEB;
    cursor: pointer;
}

.circle.answered {
    background-color: #424242;
}

.title-choices {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
    gap: 8px;
}

.title-choice-button {
    height: 56px;
    border: none;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    background-color: #EBEBEB;
    color: #5D5A51;
    font-size: 16px;
    font-weight: bold;
    transition: background-color 0.3s;
}

.title-choice-button:hover, .title-choice-button.selected:hover {
    background-color: #5a5851;
    color: #fff;
}

.title-choice-button.selected {
    background-color: #424242;
    color: #fff;
}

.title-choice-button:hover {
    background-color: #d0d0d0;
    color: #5D5A51;
}

.action-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 56px;
    padding: 0 16px; /* Padding on the sides to give a little space. Adjust as needed. */
    gap: 8px;
}

.prev-arrow, .next-arrow {
    height: 56px;
    width: 33.33%; /* Dividing equally between the three buttons */
    background: none;
    border: none;
    border-radius: 4px;
    color: #979797;
    font-size: 24px; /* Size of the arrows */
    cursor: pointer;
    transition: 0.3s;
    align-items: center;
    display: flex;
    justify-content: center;
}

.submit-btn {
    height: 56px;
    width: 33.33%; /* Dividing equally between the three buttons */
    background-color: #F7F7F7; /* Default inactive state */
    color: #BFBFBF; /* Default inactive state text color */
    border: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s;
}

/* For the active state of the submit button */
.submit-btn.active {
    background-color: #6349FF;
    color: #FFFFFF;
}

.prev-arrow:hover, .next-arrow:hover {
    background-color: #F7F7F7;
}

.submit-btn:hover:not(.active) {
   cursor: not-allowed;
}

.submit-btn.active:hover {
    background-color: #5740E0; /* Darker shade of blue for active button hover state */
}