@import url('https://fonts.googleapis.com/css2?family=Bangers&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bangers&display=swap');

.App {
  text-align: center;
  width: 400px;
  height: 862px;
  max-height: 852px;
  margin: 0 auto; /* This will center the app horizontally */
  padding: 24px 16px 24px 16px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2); /* Optional: To give the app a little elevation on desktop screens */
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

h1 {
  color: #5D5A51;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: bold;
}

h2 {
  color: #5D5A51;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 600;
}

h3 {
  color: #C1C1C1;
  font-family: "Roboto", sans-serif;
  font-size: 11px;
  font-weight: 600;
}

p {
  color: #5D5A51;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (min-width: 1024px) {
  .App {
      max-height: 852px;
  }
}

